import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { userStore } = this.props.stores;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon } = userStore;
		const { teams } = user;
		const allTeams = userStore.findTeams(teams);
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
					<div class='col-12'>
						<Error message={emailError} icon={emailErrorIcon} remove={this.resetEmailError} />
                        <Message message={emailMessage} icon={emailMessageIcon} remove={this.resetEmailMessage}  />

						<h5><Text id='users.title'>Din profil</Text></h5>
					</div>
					<div class='col-4 text-muted text-right'><Text id='users.email'>E-post</Text></div><div class='col-8'>{user.email}</div>
					{user.team && <><div class='col-4 text-muted text-right'><Text id='users.main-team'>Hovedteam</Text></div><div class='col-8'>{userStore.findTeam(user.team)}</div></>}
					{user.teams && <><div class='col-4 text-muted text-right'><Text id='users.all-teams'>Alle teamene</Text></div><div class='col-8'>{allTeams.map(team => (
						<div>
							{team.name}
							{/* <span class='text-muted'>
								<i class='fas fa-dog ml-3' /> {team.dogs.length}
								<i class='fas fa-users ml-3' /> {team.members.length}
							</span> */}
						</div>
					))}</div></>}
					{user.username && <><div class='col-4 text-muted text-right'><Text id='users.username'>Brukernavn</Text></div><div class='col-8'>{user.username}</div></>}
					{user.firstname && <><div class='col-4 text-muted text-right'><Text id='users.firstname'>Fornavn</Text></div><div class='col-8'>{user.firstname}</div></>}
					{user.lastname && <><div class='col-4 text-muted text-right'><Text id='users.lastname'>Etternavn</Text></div><div class='col-8'>{user.lastname}</div></>}
					{user.cellphone && <><div class='col-4 text-muted text-right'><Text id='users.cellphone'>Mobil</Text></div><div class='col-8'>{user.cellphone}</div></>}
					{user.url && <><div class='col-4 text-muted text-right'><Text id='users.homepage'>Hjemmeside</Text></div><div class='col-8'>{user.url}</div></>}
					{user.facebook && <><div class='col-4 text-muted text-right'><Text id='users.facebook'>Facebook</Text></div><div class='col-8'>{user.facebook}</div></>}
					{user.instagram && <><div class='col-4 text-muted text-right'><Text id='users.instagram'>Instagram</Text></div><div class='col-8'>{user.instagram}</div></>}
					{user.snapchat && <><div class='col-4 text-muted text-right'><Text id='users.snapchat'>Snapchat</Text></div><div class='col-8'>{user.snapchat}</div></>}
				</div>
			</div>
		);
	}
}

export default Profile;
